/* General Styles */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #1c1c1c; /* Dark background for the whole app */
  color: #e0e0e0; /* Lighter text for better contrast */
}

/* Main Layout */
.App {
  text-align: left;
  background-color: #1c1c1c; /* Dark background */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Content */
.content {
  flex: 1;
  padding: 20px;
}

/* Footer */
.footer {
  background-color: #333; /* Slightly lighter than the main background */
  color: white;
  text-align: center;
  padding: 20px 0;
}

/* Links */
a {
  color: #f0db4f; /* Accent color for links */
  text-decoration: none;
  transition: color 0.3s ease; /* Smooth transition for hover effect */
}

a:hover {
  color: #e0e0e0; /* Lighter color on hover */
  text-decoration: none;
}
